<template lang="pug">
  .chat-room
    hd-bar.hd-bar
      span( slot='sec-l', @click="tap")
        span.iconfont.icon-fanhui
      span( slot='default')
        h1.nickname 毛毛
        p.online-type 手机在线-4G
      span( slot="sec-r")
        span.iconfont.icon-weibiaoti-
        span.iconfont.icon-yonghu
    .chat-room-wapper
      cube-scroll.cube-scroll(ref="scroll", :data="messages", :options="options")
        .chat-window
          chat-message-box( v-for="message in messages", :is-self="message.isSelf", :message="message.message") 
    .chat-input-section( @keydown.enter="sendMsg")
      input( type="text", v-model="message") 
</template>

<script>
import hdBar from '@/components/hd-bar'
import chatMessageBox from '@/components/chat-message-box'
import { BetterScroll } from 'cube-ui'
import { socketEmit, socket } from '@/socket/socket';

export default {
  components: {
    hdBar,
    chatMessageBox,
    BetterScroll
  },
  data() {
    return {
      options: {
        scrollbar: true,
        fade: true
      },
      message: '',
      messages: [{

      }]
    }
  },
  mounted() {
    
  },
  created() {
      socketEmit('getPrivateMsg', { to: this.$route.params.id }).then(res => {
        let data =res.data;
        this.messages = data.reverse().map((msg) => {
          return {
            message: msg.content,
            isSelf: this.$route.params.id !== msg.creater
          }
        })
      })
     socket.on('message', (a, b) => {
        this.messages.push({message: a , isSelf: false})
        this.scrollToBt();
     })
  },
  mounted() {
    this.scrollToBt();
  },
  methods: {
    scrollToBt() {
      this.$nextTick(() => {
        // let scroll = this.$refs['scroll'].scroll;
        // let maxScrollY = scroll.maxScrollY;
        // scroll.scrollTo(0, -200, 0)
      })
    },
    tap() {
      this.$router.go(-1)
    },
    sendMsg() {
      this.messages.push({
        message: this.message,
        isSelf: true
      })
      socketEmit('newMessage', { content: this.message, to: this.$route.params.id })
      this.message = '';
      this.scrollToBt();
      
    }
    
  }

}
</script>
<style lang="stylus" scoped>
  .chat-room 
    background-color #f1f2f7
  .hd-bar 
    .nickname
      margin-bottom 0.4rem
    .online-type 
      font-size 12px   
  .chat-room-wapper 
    padding-top $hd-bar-height
    height calc(100vh - 3rem)
  .chat-input-section
    height: 3rem
    background #f2f2f2
    z-index 50 
    width 100%
    padding .4rem .4rem
    input 
      outline none
      width 100% 
      padding 3px 10px
      border none 
      background-color #ffffff 
      border-radius 3px 

</style>




