<template lang="pug">
  div.chat-message-box( :class="[isSelf? 'one-self': '']" )
    div.headimg 
      img( src="../images/touxiang.jpg")
    
    div.message {{message}}
</template>
<script>
export default {
  name: 'chat-message-box',
  props: {
    isSelf: {
      type: Boolean,
      default: true
    },
    info: {
      type: Object,
      default() {
        return {}
      }
    },
    message: {

    }
  }
}
</script>
<style lang="stylus" scoped>
  .chat-message-box 
    padding 0.55rem 0.5rem 
    display flex
    &.one-self  
      flex-direction: row-reverse;
    .headimg 
      height 2.5rem 
      width 2.5rem 
      border-radius 50% 
      img 
        border-radius 50%  
        width 100% 
        height 100% 
    .message 
      margin 0 0.5rem
      max-width 14.35rem 
      min-width 0 
      padding 0.8rem 
      background-color #b8e4fd 
      border-radius 4px


</style>





